















































import Vue from "vue";
export default Vue.extend({
  data: () => ({
    date: new Date().toISOString().substr(0, 7),
    menu: false,
    file: new Blob()
  }),
  methods: {
    uploadVectorData() {
      const date = this.date.replace("-", "/").replace("-", "/");
      const formData = new FormData();
      formData.append("file", this.file);
      fetch("/api/vectordataupload/" + date, {
        method: "post",
        body: formData
      })
        .then(r => {
          this.file = new Blob();
        })
        .catch(console.error);
    },
    syncSCUD(){
      fetch("/api/syncscud", {
        method: "post"
      }).then();
    }
  }
});
